<template>
  <div class="user-edit-container">
    <!-- Header 区域 -->
    <van-nav-bar title="编辑资料" left-arrow @click-left="$router.back()" fixed />

    <!-- 用户资料 -->
    <van-cell-group class="action-card">
      <van-cell title="头像" is-link center>
        <template #default>
          <van-uploader :after-read="afterRead">
            <van-image round class="avatar" :src="$store.state.userPhoto" />
          </van-uploader>
        </template>
      </van-cell>

      <van-cell title="名称" is-link :value="userProfile.name"
        @click="show = true" />
      <van-cell title="生日" is-link :value="userProfile.birthday"
        @click="isShow=true" />

    </van-cell-group>

    <!-- 名称编辑 -->
    <van-dialog v-model="show" title="修改名称" show-cancel-button
      :beforeClose="beforeCloseFn">
      <input type="text" v-model.trim="userName" v-gfocus placeholder="请输入名称">
    </van-dialog>

    <!-- 生日编辑 -->
    <van-popup v-model="isShow" position="bottom" round
      :style="{ height: '50%' }">
      <van-datetime-picker v-model="currentDate" type="date" title="选择年月日"
        :min-date="minDate" :max-date="maxDate" v-show="isShow"
        @confirm="editBirthdayFn" @cancel="isShow=false" />
    </van-popup>
  </div>

</template>

<script>
import { getUserProfileAPI, editUserPhotoAPI, editUserProfileAPI } from '@/api'
import Notify from '@/ui/Notify.js'
import { formatDate } from '@/utils/date'
import { mapMutations } from 'vuex'
export default {
  name: 'UserEdit',

  data () {
    return {
      userProfile: {},
      show: false, // 姓名弹出框状态
      isShow: false, // 生日弹出框状态
      minDate: new Date(1920, 0, 1),
      maxDate: new Date(),
      currentDate: new Date(),
      userName: ''
    }
  },
  async created () {
    const res = await getUserProfileAPI()
    // console.log(res)
    this.userProfile = res.data.data
    this.userName = res.data.data.name
    this.currentDate = new Date(res.data.data.birthday)
  },
  methods: {
    // vuex 调用
    ...mapMutations(['SET_USERPHOTO', 'SET_USERNAME']),
    // 头像上传
    async afterRead (file) {
      // 此时可以自行将文件上传至服务器
      // console.log(file)
      const formData = new FormData()
      formData.append('photo', file.file)
      const res = await editUserPhotoAPI(formData)
      this.userProfile.photo = res.data.data.photo
      // 把头像传给 vuex中的state
      // this.$store.commit('SET_USERPHOTO',
      //   this.userInfo.photo)
      this.SET_USERPHOTO(res.data.data.photo)
    },

    // 名字编辑
    // async userNameEditFn () {
    //   const res = await editUserProfileAPI({
    //     name: this.userName
    //   })
    //   console.log(res)
    //   this.userProfile.name = this.userName // 更新成功 显示到页面
    // },
    async beforeCloseFn (action, done) {
      if (action === 'confirm') {
        // 确定
        const reg = /^[a-zA-Z0-9\u4e00-\u9fa5]{1,7}$/
        if (reg.test(this.userName) === true) { // 通过校验
          // this.userNameEditFn() // 调用更新接口
          await editUserProfileAPI({
            name: this.userName
          })
          this.userProfile.name = this.userName // 更新成功 显示到页面
          this.SET_USERNAME(this.userName)
          done()
        } else { // 没通过校验
          Notify({ type: 'warning', message: '用户名只能是1-7位中英数字组合' })
          done(false)
        }
      } else {
        // 取消 关闭弹窗
        done()
      }
    },

    // 生日编辑
    async editBirthdayFn (value) {
      // console.log(value)
      const date = formatDate(value) // 时间格式化
      // console.log(date)
      await editUserProfileAPI({
        birthday: date
      })
      this.userProfile.birthday = date // 更新 界面显示
      this.isShow = false
    }
  }
}
</script>

<style lang="less" scoped>
.user-edit-container {
  padding-top: 46px;
  .avatar {
    width: 50px;
    height: 50px;
  }
}
/deep/ .van-dialog__content {
  text-align: center;
  input {
    height: 50px;
    text-align: center;
    border: none;
    outline: none;
    padding: 0;
    font-size: 14px;
  }
}
</style>
